<template>
  <div class="r-video">
    <!-- 播放器界面; 兼容ios  controls-->
    <video ref="video" v-if="showVideo" webkit-playsinline="true" playsinline="true"
           x-webkit-airplay="true" x5-video-player-type="h5" x5-video-player-fullscreen="true"
           x5-video-orientation="landscape" style="object-fit:fill" controlslist="nodownload"
           preload="false" :poster="mediaPoster" :src="mediaSrc" @waiting="handleWaiting"
           @canplaythrough="state.isLoading = false"
           @playing="state.isLoading = false, state.controlBtnShow = false, state.playing=true"
           @stalled="state.isLoading = true" @error="handleError">
      您的浏览器不支持HTML5
    </video>
    <!--
  object-fit: fill   视频内容充满整个video容器
  poster:"img.jpg" 视频封面
  autoplay： 自动播放
     auto - 当页面加载后载入整个视频
     meta - 当页面加载后只载入元数据
     none - 当页面加载后不载入视频

  muted：当设置该属性后，它规定视频的音频输出应该被静音
  webkit-playsinline playsinline:   内联播放
  x5-video-player-type="h5" :  启用x5内核H5播放器
  x5-video-player-fullscreen="true"  全屏设置。ture和false的设置会导致布局上的不一样
  x5-video-orientation="portraint" ：声明播放器支持的方向，可选值landscape 横屏,portraint竖屏。
                                     默认值portraint。无论是直播还是全屏H5一般都是竖屏播放，
                                     但是这个属性需要x5-video-player-type开启H5模式
    -->
    <!-- 兼容Android端层级问题, 弹出层被覆盖 -->
    <img v-show="!showVideo || state.isEnd" class="poster" :src="mediaPoster" :onerror="`onerror=null;src='${defaultPost}'`" alt/>
    <!--    :onerror="`onerror=null;src='${defaultPost}'`"-->
    <!-- 控制窗口 -->
    <div class="control" v-show="!state.isError" ref="control" @touchstart="touchEnterVideo"
         @touchend="touchLeaveVideo">
      <!-- 播放 || 暂停 || 加载中-->
      <div class="play" @touchstart.stop="clickPlayBtn" v-show="state.controlBtnShow">
        <img v-show="!state.playing && !state.isLoading"
             src="./../../assets/video/content_btn_play.svg" alt=""/>
        <img v-show="state.playing  && !state.isLoading"
             src="./../../assets/video/content_btn_pause.svg" alt=""/>
        <div class="loader" v-show="state.isLoading">
          <div class="loader-inner ball-clip-rotate">
            <div></div>
          </div>
        </div>
      </div>
      <!-- 控制条 -->
      <!-- <div class="control-bar" v-show="state.controlBarShow"> -->
      <div class="control-bar" :style="{ visibility: state.controlBarShow ? 'visible' : 'hidden'}">
        <span class="time">{{video.displayTime}}</span>
        <span class="progress" ref="progress">
          <img class="progress-btn ignore"
               :style="{transform: `translate3d(${video.progress.current}px, 0, 0)`}"
               src="./../../assets/video/content_ic_tutu.svg" alt=""/>
          <span class="progress-loaded" :style="{ width: `${video.loaded}%`}"></span>
          <!-- 设置手动移动的进度条 -->
          <span class="progress-move" @touchmove.stop.prevent="moveIng($event)"
                @touchstart.stop="moveStart($event)" @touchend.stop="moveEnd($event)"></span>
        </span>

        <span class="total-time">{{video.totalTime}}</span>
        <span class="full-screen" @click="fullScreen">
          <img src="./../../assets/video/content_ic_increase.svg" alt/>
        </span>
      </div>
    </div>
    <!-- 错误弹窗 -->
    <div class="error" v-show="state.isError">
      <p class="lose">视频加载失败</p>
      <p class="retry" @click="retry">点击重试</p>
    </div>
  </div>
</template>
<script>
import { throttle } from "@/utils/tool";

const pad = val => {
  val = Math.floor(val);
  if (val < 10) {
    return "0" + val;
  }
  return val + "";
};
const timeParse = sec => {
  let min = 0;
  min = Math.floor(sec / 60);
  sec = sec - min * 60;
  return pad(min) + ":" + pad(sec);
};

export default {
  name: "r-video",
  props: {
    src: {
      type: String,
      required: true
    },
    poster: {
      type: String,
      default: ""
    },
    showVideo: {
      // 兼容android弹出层
      type: Boolean,
      default: true
    },
    autoPlay: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      defaultPost: require("@/assets/video/videopost.png"),
      mediaSrc: "",
      mediaPoster: "",
      // video元素
      $video: null,
      // 视频容器元素
      player: {
        $player: null,
        pos: null
      },
      // progress进度条元素
      progressBar: {
        $progress: null, // 进度条DOM对象
        pos: null
      },
      // video控制显示设置
      video: {
        loaded: 0, // 缓存长度
        displayTime: "00:00", // 进度时间
        totalTime: "00:00", // 总时间
        progress: {
          width: 0, // 进度条长度
          current: 0 // 进度条当前位置
        }
      },
      // 定时器
      hideTimer: null,
      // 播放状态控制
      state: {
        controlBtnShow: true, // 播放按钮
        controlBarShow: false, // 控制条
        fullScreen: false,
        playing: false,
        isLoading: false,
        isEnd: false,
        isError: false
      },
      // 首次触摸
      isFirstTouch: true,
      // 暂停状态触摸
      isPauseTouch: false
    };
  },
  mounted () {
    this.$nextTick(() => {
      // if (this.autoPlay)
      this.init();
    });
  },
  methods: {
    init () {
      this.mediaSrc = this.src;
      this.mediaPoster = this.poster;
      // 初始化video,获取video元素
      this.$video = this.$el.getElementsByTagName("video")[0];
      this.initPlayer();
    },
    // 带参数初始化，防止参数没获取到
    initParam (src, poster) {
      this.mediaSrc = src;
      this.mediaPoster = poster;
      this.state.isError = false;
      this.$video = this.$el.getElementsByTagName("video")[0];
      this.initPlayer();
    },
    // 初始化播放器容器, 获取video-player元素
    // getBoundingClientRect()以client可视区的左上角为基点进行位置计算
    initPlayer () {
      const $player = this.$el;
      const $progress = this.$el.getElementsByClassName("progress")[0];
      // 播放器位置
      this.player.$player = $player;
      this.progressBar.$progress = $progress;
      this.player.pos = $player.getBoundingClientRect();
      this.progressBar.pos = $progress.getBoundingClientRect();
      this.video.progress.width = Math.round(
        $progress.getBoundingClientRect().width
      );
    },
    // 点击播放 & 暂停按钮
    clickPlayBtn () {
      if (this.state.isLoading) return;
      this.initPlayer();
      this.isFirstTouch = false;
      this.state.playing = !this.state.playing;
      this.state.isEnd = false;
      if (this.$video) {
        // 播放状态
        if (this.state.playing) {
          try {
            this.$video.play();
            this.$emit("selectChange");
            this.isPauseTouch = false;
            // 监听缓存进度
            this.$video.addEventListener("progress", e => {
              this.getLoadTime();
            });
            // 监听播放进度
            this.$video.addEventListener("timeupdate", throttle(this.getPlayTime, 100, 1));
            // 监听结束
            this.$video.addEventListener("ended", e => {
              // 重置状态
              this.state.playing = false;
              this.state.isEnd = true;
              this.state.controlBtnShow = true;
              this.video.displayTime = "00:00";
              this.video.progress.current = 0;
              this.$video.currentTime = 0;
              // this.mediaSrc = this.mediaSrc;
            });
          } catch (e) {
            // 捕获url异常出现的错误
          }
        } else {
          // 停止状态
          this.isPauseTouch = true;
          this.$video.pause();
        }
      }
    },

    stopPlay () {
      // this.state.isEnd = true;
      // this.isFirstTouch = true;

      this.$video.load();
      this.state.playing = false;
      this.init();
    },
    // 触碰播放区
    touchEnterVideo () {
      if (this.isFirstTouch) return;
      if (this.hideTimer) {
        clearTimeout(this.hideTimer);
        this.hideTimer = null;
      }
      this.state.controlBtnShow = true;
      this.state.controlBarShow = true;
    },
    // 离开播放区
    touchLeaveVideo () {
      if (this.isFirstTouch) return;
      if (this.hideTimer) {
        clearTimeout(this.hideTimer);
      }
      // 暂停触摸, 不隐藏
      if (this.isPauseTouch) {
        this.state.controlBtnShow = true;
        this.state.controlBarShow = true;
      } else {
        this.hideTimer = setTimeout(() => {
          this.state.controlBarShow = false;
          // 加载中只显示loading
          if (this.state.isLoading) {
            this.state.controlBtnShow = true;
          } else {
            this.state.controlBtnShow = false;
          }
          this.hideTimer = null;
        }, 3000);
      }
    },
    // 等待数据加载
    handleWaiting () {
      this.state.controlBtnShow = true;
      this.state.isLoading = true;
    },
    // 数据加载出错
    handleError () {
      // console.log(this.mediaSrc, e);
      if (this.mediaSrc) this.state.isError = true;
    },
    // 点击重新加载
    retry () {
      this.state.isError = false;
      this.$video.load();
      // this.init();
    },
    // 获取播放时间
    getPlayTime () {
      const percent = this.$video.currentTime / this.$video.duration;
      this.video.progress.current = Math.round(
        this.video.progress.width * percent
      );
      // this.state.isError = false;
      // console.log(percent, this.video.progress.width)
      // 赋值时长
      this.video.totalTime = timeParse(this.$video.duration);
      this.video.displayTime = timeParse(this.$video.currentTime);
    },
    // 获取缓存时间
    getLoadTime () {
      // console.log('缓存了...',this.$video.buffered.end(0));
      this.video.loaded =
        (this.$video.buffered.end(0) / this.$video.duration) * 100;
    },
    // 手动调节播放进度
    moveStart (e) {
    },
    moveIng (e) {
      // console.log("触摸中...");
      let currentX = e.targetTouches[0].pageX;
      let offsetX = currentX - this.progressBar.pos.left;
      // 边界检测
      if (offsetX <= 0) {
        offsetX = 0;
      }
      if (offsetX >= this.video.progress.width) {
        offsetX = this.video.progress.width;
      }
      this.video.progress.current = offsetX;

      let percent = this.video.progress.current / this.video.progress.width;
      this.$video.duration && this.setPlayTime(percent, this.$video.duration);
    },
    moveEnd (e) {
      // console.log("触摸结束...");
      let currentX = e.changedTouches[0].pageX;
      let offsetX = currentX - this.progressBar.pos.left;
      this.video.progress.current = offsetX;
      // 这里的offsetX都是正数
      let percent = offsetX / this.video.progress.width;
      this.$video.duration && this.setPlayTime(percent, this.$video.duration);
    },
    // 设置手动播放时间
    setPlayTime (percent, totalTime) {
      this.$video.currentTime = Math.floor(percent * totalTime);
    },
    // 设置全屏
    fullScreen () {
      console.log("点击全屏...");
      if (!this.state.fullScreen) {
        this.state.fullScreen = true;
        this.$video.webkitRequestFullScreen();
      } else {
        this.state.fullScreen = false;
        document.webkitCancelFullScreen();
      }
    }
  },
  beforeDestroy () {
  }
};
</script>



